import React, { Component } from 'react';
import { GENRES_LIST } from '../../core/constants';

// require('./_index.scss');

/*
  'To the left, to the left...'
  'Is this the way it's really going down?'
  'Youre rolling in the deep'
  'Cry YOU a river'
  'In the end, it doesnt really matter'
  'It was something unpredictable but in the end it was right'

  'Try again and DREAM ON!'
  'Youve been learning, Ive been learning. Now give us your love!'
  'Dont dream its over... give it another shot!'
  'Try again - It makes you a little bit stronger!'
  'Come again, you fast machine'
*/
const motivationalQuote = ['To the left, to the left...',
  'Is this the way it\'s really going down?',
  'Youre rolling in the deep',
  'Cry YOU a river',
  'In the end, it doesnt really matter',
  'It was something unpredictable but in the end it was right',
  'Try again and DREAM ON!',
  'Youve been learning, Ive been learning. Now give us your love!',
  'Dont dream its over... give it another shot!',
  'Try again - It makes you a little bit stronger!',
  'Come again, you fast machine'
];
const getMotivationalQuote = () => motivationalQuote[~~(Math.random() * motivationalQuote.length)];
const getFormattedGenreName = (genreName) => {
  return genreName === '90' || genreName === '00'
    ? genreName + 's'
    : genreName;
};

const Results = ({ score, songName, onContinue, onShare, onLogin, genre, loginStatus }) => (
  <div className="Results wrapper-container" >
    <div className="results-fix">
      <div className="image"></div>
      <h1 className="text-wrapper">You failed!</h1>
      <div className="correct-answer"><i>{getMotivationalQuote()}</i></div>
      <div className="correct-answer">That song was <strong className="song">{songName}</strong></div>
      <div className="score">
        <div className="correct-answer">You made</div>
        <div className="text-wrapper">{score} pts</div>
        in <strong>{getFormattedGenreName(GENRES_LIST[genre].name)}</strong> music
      </div>
      <button className="play-again-cta" onClick={onContinue}>Rewind And Try Again &rsaquo;</button>

      { loginStatus ?
        <div className="social-buttons">
          <a href={`./#/leaderboard/${genre}`} className="leaderboard-btn">View leaderboard</a>
          <div className="share-btn" onClick={() => {onShare(score, genre)}}>Share</div>
        </div>
        :
        <div className="social-buttons">
          <div className="leaderboard-btn" onClick={() => {onLogin({score, genre}, false, genre)}}>View leaderboard</div>
          <div className="share-btn" onClick={() => {onLogin({score, genre}, true)}}>Share</div>
          <div className="login-btn" onClick={() => {onLogin({score, genre})}}>Login</div>
        </div>
      }

    </div>
  </div>
);

export default Results;



